<script>
	import ffButton from "@/components/UI/ff-button.vue"
	import axios from 'axios'
	import stickyBtn from '@/components/UI/stickyBtn.vue'
	import ficList from '@/pages/ficList.vue'


	export default {
		name: 'libraryPage',
		components:{
			ffButton,
			stickyBtn,
			ficList
			
		},

		props:{
			
		},

		data() {
			return{			

			searchQuery:'',
			isFicSelected: false,
			selectedChapter: '',
            chapterOptions: [
                    {value: 'chapter', name: 'chapter.name'}
                ],
            selectedFic: this.$store.state.libArray,
            sortedFics: this.$store.state.libArray,
            
            
			}
		},
		computed:{
			
        	sortedandSearchedFics(){

        		return this.sortedFics.filter(fic => fic.name.toLowerCase().includes(this.searchQuery.toLowerCase()));

        	},
        	filteredandSearchedFics(){

        		return this.$store.state.filteredByGenre.filter(fic => fic.name.toLowerCase().includes(this.searchQuery.toLowerCase()));

        	},


        },
		            
        methods:{  
    		selectFic(fanfic) {
            this.selectedFic = this.sortedFics.filter(f => f.id == fanfic.id)
            this.isFicSelected = true;
           	this.$store.state.bookshelfVisible = false;
           	window.location.hash="ficView";

        	},
        	filterByGenre(genre){
        		this.$store.state.filteredByGenre = this.sortedFics.filter((fic) => {
        			
        			return fic.genres.includes(genre);
                    
        		})
        		console.log(this.$store.state.filteredByGenre)
                this.$store.state.noFilter = false;
                this.$store.state.genreFilter = true;
                this.$store.state.bookshelfVisible = true;
        		console.log(this.bookshelfVisible)
        	},
        	async fetchFic(fanfic){
              
                /*try{
                  this.$store.state.isFicLoading = true;
                  for (let index = 0; index < this.$store.state.libArray.length; index++){
                    for (let i = 0; i < this.$store.state.libArray[index].chapters.length; i++){
                        const res = await fetch(this.$store.state.libArray[index].chapters[i].path);

                        this.$store.state.libArray[index].chapters[i].content = await res.text();                      
                    }
                  }
        
                } catch (e) {
                    console.log(e)
                } finally {
                  this.$store.state.isFicLoading = false;
                }*/
                try{
                  this.$store.state.isFicLoading = true;
                  
                    for (let i = 0; i < fanfic.chapters.length; i++){
                        const res = await fetch(fanfic.chapters[i].path);

                        fanfic.chapters[i].content = await res.text();                      
                    }
                  
        
                } catch (e) {
                    console.log(e)
                } finally {
                  this.$store.state.isFicLoading = false;
                }
    },
        	
        },
        mounted(){
        	
        	
        }
       
	}

</script>

